  transition: transform 0.4s linear;
  flex-shrink: 0;
  width: 20px;
  transform: scaleY(-1);
  aspect-ratio: 1;
}
.select__current {
  flex-grow: 1;
  overflow: hidden;
  width: 100%;
  display: flex;
  align-items: center;
  grid-gap: 7px;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-family: var(--font-second);
  font-weight: 500;
  color: rgba(var(--cl-c), 1);
  transition: all 0.3s;
  font-size: clamp(16px, 1.2879vw, 17px);
  line-height: 118%;
  letter-spacing: -0.02em;
}
.select__current:hover {
  color: rgba(var(--cl-c), 0.8);
}
.select__body {
  right: -2px;
  position: absolute;
  z-index: 5;
  height: 0;
  top: calc(100% + 5px);
  overflow: hidden;
  transition: height 0.1s ease-in;
  left: -2px;
  border: solid 1px transparent;
  border-radius: 8px;
}
.select__body i {
  margin: 0;
  text-transform: uppercase;
  font-size: inherit;
  line-height: inherit;
}
.select__list {
  overflow-y: auto;
  background: rgba(var(--bg-a), 1);
  padding: 4px 0;
  display: flex;
  flex-direction: column;
}
.select__list::-webkit-scrollbar-thumb {
  background-color: rgba(47, 47, 47, 0.4);
  border-radius: 8px;
}
.select__list::-webkit-scrollbar-track {
  background-color: transparent;
}
.select__list::-webkit-scrollbar {
  width: 3px;
  border-radius: 99px;
}
.select__option {
  display: flex;
  padding: 10px 12px;
  align-items: center;
  grid-gap: 5px;
  width: 100%;
  cursor: pointer;
  line-height: 100%;
  transition: all 0.3s;
  font-family: var(--font-second);
  font-weight: 500;
  font-size: clamp(16px, 1.2879vw, 17px);
  line-height: 118%;
  letter-spacing: -0.02em;
  color: rgba(var(--cl-c), 1);
}
.select__option:hover {
  background-color: rgba(var(--bg-c), 0.04);
}
.select__option:active {
  background-color: rgba(var(--bg-c), 0.08);
}
.select__option a {
  display: flex;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
}

.select.active {
  z-index: 30;
  background: rgba(47, 47, 47, 0.12);
  border: 1px solid rgba(47, 47, 47, 0.8);
}
.select.active .select__body {
  box-shadow: 0 24px 48px 0 rgba(50, 50, 71, 0.06), 0 12px 24px 0 rgba(50, 50, 71, 0.04);
}
.select.active .select__header > svg {
  transform: scaleY(1);
}

