.main-button {
  padding: clamp(15px, 1.389vw, 20px) 0;
  background-color: rgba(var(--bg-a), 1);
  border: 1px solid rgba(var(--cl-f), 1);
  border-radius: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  font-family: var(--font-second);
  font-size: 20px;
  font-weight: 700;
  letter-spacing: 0.02em;
  color: rgba(var(--cl-f), 1);
  text-align: center;
  transition: all 0.3s ease-in-out;
}
.main-button:hover {
  color: rgba(var(--bg-a), 1);
  background-color: rgba(var(--bg-f), 1);
}
.main-button--green {
  padding: clamp(15px, 1.736vw, 25px) 0;
  font-family: var(--second-family);
  font-weight: 500;
  font-size: 18px;
  line-height: 167%;
  letter-spacing: -0.02em;
  color: rgba(var(--cl-a), 1);
  background: linear-gradient(180deg, #0d7768 0%, #006758 100%);
  padding: 13px 24px;
  border-color: transparent;
  position: relative;
  z-index: 1;
}
.main-button--green::before {
  content: "";
  inset: 0;
  border-radius: inherit;
  z-index: -1;
  position: absolute;
  background-color: rgba(var(--bg-a), 1);
  transition: all 0.2s;
  opacity: 0;
  visibility: hidden;
}
.main-button--green:hover {
  color: rgba(var(--cl-d), 1);
}
.main-button--green:hover::before {
  opacity: 1;
  visibility: visible;
}

.details {
  display: flex;
  align-items: center;
  gap: 10px;
}
.details li {
  display: flex;
  align-items: center;
  gap: 5px;
  color: rgba(var(--cl-c), 0.6);
}
.details li p {
  font-size: 14px;
  line-height: 129%;
  color: rgba(var(--cl-c), 0.6);
}
.details li:first-child svg {
  fill: currentColor;
  stroke: transparent;
}
.details li svg {
  stroke: currentColor;
}
.details li.author svg {
  fill: transparent;
  stroke: currentColor;
}
.details li.readtime svg {
  fill: transparent;
  stroke: currentColor;
}
.details li.pubtime svg {
  fill: currentColor;
  stroke: transparent;
}

.articles {
  width: 100%;
}
.articles__items {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: clamp(16px, 3.125vw, 32px);
  overflow: hidden;
  padding: 16px;
  margin: -16px;
}
@media (max-width: 1024px) {
  .articles__items {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 767px) {
  .articles__items {
    grid-template-columns: 100%;
  }
}
.articles__item {
  display: flex;
  flex-direction: column;
  gap: 16px;
  padding: 16px;
  margin: -16px;
  border: 1px solid rgba(var(--br-a), 0);
  background-color: rgba(var(--bg-a), 0);
  transition: all 100ms ease-in-out 100ms;
  border-radius: 24px;
}
.articles__item:hover {
  border-radius: 24px;
  border: 1px solid rgba(var(--br-a), 1);
  background-color: rgba(var(--bg-a), 1);
}
.articles__item:hover .articles__title {
  color: rgba(var(--cl-d), 1);
}
.articles__item:hover .articles__image picture,
.articles__item:hover .articles__image img {
  transform: scale(1.05);
}
.articles__top {
  position: relative;
}
.articles__image {
  display: flex;
  overflow: hidden;
  border-radius: 16px;
  transition: all 0.4s ease;
}
.articles__image picture,
.articles__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 16px;
  transition: all 0.4s ease;
  transition-duration: 1s;
}
.articles__tags {
  position: absolute;
  left: 10px;
  bottom: 10px;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 4px;
}
.articles__tags a {
  padding: 2px 6px 4px 6px;
  background: rgba(var(--bg-b), 0.4);
  border-radius: 4px;
  font-family: var(--font-primary);
  font-size: 14px;
  line-height: 114%;
  font-weight: 700;
  letter-spacing: -0.02em;
  color: rgba(var(--cl-a), 1);
  transition: all 0.4s ease;
}
.articles__tags a:hover, .articles__tags a:active {
  background: rgba(var(--bg-b), 1);
}
.articles__info {
  padding-top: 16px;
  display: flex;
  flex-direction: column;
  gap: 8px;
}
.articles__title {
  font-family: var(--font-primary);
  font-size: clamp(20px, 1.667vw, 24px);
  line-height: 133%;
  font-weight: 700;
  letter-spacing: -0.02em;
  transition: all 0.4s ease;
}
.articles__more {
  margin: clamp(24px, 5.469vw, 56px) auto 0;
  padding: 14px 56px;
  display: flex;
  align-items: center;
  gap: 10px;
  font-family: var(--font-second);
  font-size: 18px;
  line-height: 167%;
  font-weight: 500;
  letter-spacing: -0.02em;
  color: rgba(var(--cl-d), 1);
  background: rgba(var(--bg-a), 1);
  border: 2px solid rgba(var(--cl-d), 1);
  border-radius: 100px;
  transition: all 0.4s ease;
}
.articles__more svg {
  stroke: currentColor;
}
.articles__more:hover {
  padding: 14px 66px;
}
.articles__more:active {
  background-color: rgba(var(--bg-g), 1);
}

.quiz {
  position: relative;
  z-index: 2;
}
.quiz__top-frame {
  height: 32px;
  clip-path: polygon(0% 100%, 100% 100%, 100% 64.92%, 100% 64.92%, 99.952% 57.655%, 99.813% 50.755%, 99.591% 44.311%, 99.292% 38.415%, 98.925% 33.156%, 98.497% 28.626%, 98.014% 24.917%, 97.485% 22.119%, 96.917% 20.324%, 96.318% 19.622%, 3.808% 0.776%, 3.808% 0.776%, 3.193% 1.257%, 2.609% 2.892%, 2.063% 5.587%, 1.564% 9.245%, 1.119% 13.77%, 0.738% 19.068%, 0.427% 25.043%, 0.195% 31.6%, 0.05% 38.642%, 0% 46.074%, 0% 100%);
  background-color: white;
}
.quiz__bottom-frame {
  height: 32px;
  clip-path: polygon(0% 33.098%, 0% 33.098%, 0.047% 39.048%, 0.183% 44.706%, 0.401% 49.997%, 0.694% 54.848%, 1.055% 59.184%, 1.477% 62.932%, 1.952% 66.019%, 2.473% 68.37%, 3.034% 69.912%, 3.626% 70.571%, 96.136% 100%, 96.136% 100%, 96.758% 99.686%, 97.349% 98.394%, 97.903% 96.204%, 98.409% 93.198%, 98.861% 89.455%, 99.249% 85.058%, 99.565% 80.085%, 99.801% 74.619%, 99.949% 68.739%, 100% 62.527%, 100% 1.21%, 0% 1.21%, 0% 33.098%);
  background-color: white;
}
.quiz__content {
  margin: -2px 0;
  padding: 4px 0;
  position: relative;
  display: grid;
  grid-template-columns: 1fr clamp(200px, 15.139vw, 218px);
  background-color: white;
}
@media (max-width: 767px) {
  .quiz__content {
    grid-template-columns: 100%;
  }
}
.quiz__info {
  padding: 0 24px;
  display: flex;
  flex-direction: column;
  gap: 16px;
}
.quiz__duration {
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 14px;
  line-height: 129%;
  letter-spacing: -0.02em;
}
.quiz__title {
  font-family: var(--font-primary);
  font-size: 24px;
  line-height: 133%;
  font-weight: 700;
  letter-spacing: -0.02em;
  transition: all 0.4s ease;
}
.quiz__btn {
  position: relative;
  width: fit-content;
  padding: 12px 16px 12px 20px;
  background: linear-gradient(315deg, #0a8bd3 0%, #2ab2ff 100%);
  border-radius: 100px;
}
.quiz__btn span {
  position: relative;
  z-index: 5;
  display: flex;
  align-items: center;
  gap: 10px;
  font-family: var(--font-second);
  font-size: 16px;
  line-height: 150%;
  font-weight: 500;
  letter-spacing: -0.02em;
  color: rgba(var(--cl-a), 1);
  transition: all 0.4s ease;
}
.quiz__btn span svg {
  stroke: rgba(var(--cl-a), 1);
}
.quiz__btn::before {
  content: "";
  position: absolute;
  inset: 0;
  background: linear-gradient(315deg, #1ba0eb 0%, #2ab2ff 100%);
  border-radius: 100px;
  opacity: 0;
  transition: all 0.4s ease;
  z-index: -1;
}
.quiz__btn::after {
  content: "";
  position: absolute;
  inset: 0;
  background: linear-gradient(315deg, rgba(27, 160, 235, 0.8) 0%, rgba(42, 178, 255, 0.8) 100%);
  border-radius: 100px;
  opacity: 0;
  transition: all 0.4s ease;
  z-index: -1;
}
.quiz__picture {
  position: relative;
}
@media (max-width: 767px) {
  .quiz__picture {
    position: absolute;
    right: 0;
    top: -10%;
    width: clamp(155px, 28.646vw, 220px);
    height: 155px;
  }
}
.quiz__image {
  position: absolute;
  display: flex;
  width: clamp(280px, 21.597vw, 311px);
  height: clamp(280px, 21.597vw, 311px);
  z-index: 2;
  left: -50px;
  top: -100px;
  transform: rotate(-5deg);
  transition: all 0.3s ease-in;
}
@media (max-width: 767px) {
  .quiz__image {
    position: absolute;
    width: 100%;
    height: 100%;
    left: initial;
    top: -85px;
    transform: rotate(0deg);
  }
}
.quiz:hover .quiz__title {
  color: rgba(var(--cl-e), 1);
}
.quiz:hover .quiz__image {
  transform: rotate(-7.5deg);
}
.quiz:hover .quiz__btn::before {
  opacity: 1;
  z-index: 0;
}
.quiz:active .quiz__title {
  color: #0876b4;
}
.quiz:active .quiz__btn::after {
  opacity: 1;
  z-index: 1;
}

.pagination {
  display: flex;
  align-items: center;
  justify-content: center;
  grid-gap: 12px;
}
.pagination__list {
  display: flex;
  align-items: center;
  grid-gap: clamp(5px, 0.868vw, 10px);
}
.pagination__list-link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 48px;
  height: 48px;
  border: 2px solid transparent;
  border-radius: 100px;
  font-size: 16px;
  line-height: 125%;
  font-weight: 500;
  letter-spacing: -0.02em;
  transition: all 0.4s ease-in-out;
  cursor: pointer;
}
.pagination__list-link:hover {
  transform: scale(1.1);
  color: rgba(var(--cl-d), 1);
  border: 2px solid rgba(var(--bg-d), 1);
  background-color: #EFE7DD;
}
.pagination__list-link.active, .pagination__list-link:active {
  color: rgba(var(--cl-d), 1);
  border: 2px solid rgba(var(--bg-d), 1);
}
.pagination__nav {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  color: rgba(var(--cl-d), 1);
  border: 2px solid rgba(var(--bg-d), 1);
  border-radius: 100px;
  transition: all 0.4s ease-in-out;
}
.pagination__nav svg {
  stroke: currentColor;
}
.pagination__nav--prev {
  transform: scale(-1);
}
.pagination__nav.inactive {
  border: 2px solid var(--br-c);
  color: var(--br-c);
  opacity: 0.5;
  pointer-events: none;
}
.pagination__nav:hover {
  transform: scale(1.1);
  background-color: #EFE7DD;
}
.pagination__nav.active, .pagination__nav:active {
  color: rgba(var(--cl-d), 1);
  background-color: #EFE7DD;
}

.feedback {
  margin-left: -55px;
  position: relative;
  padding: clamp(32px, 2.674vw, 38.5px) clamp(34px, 4.306vw, 62px);
  display: flex;
